html {
	font-family: 'Poppins', sans-serif;

}
body {
	height: 100%;
	font-family: 'Poppins', sans-serif;
}

.header-color {
    background: #0f0c29;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #002651, #002651, #002651);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #002651, #002651, #002651); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    

}

.landing-grid {
    background-image: linear-gradient(165deg, #002651 31.41%, #775ada 31.41%, #775ada 50%, #002651 50%, #002651 81.41%, #775ada 81.41%, #775ada 100%);
background-size: 4000px 2000px;

    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}

.landing-block {
    color: black;
    border-radius: 33% 67% 68% 32% / 30% 32% 68% 70%;
}

.banner-text {
    opacity: .8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
    
}

.landing-button {
    
   border-radius: 4px;
   background: linear-gradient(to right, #f7b733, #fc4a1a) !important;
   border: none;
   color: #FFFFFF;
   text-align: center;
   text-transform: uppercase;
   font-size: 22px;
   padding: 20px;
   width: 200px;
   transition: all 0.4s;
   cursor: pointer;
   margin: 20px;
   
}

.landing-button:hover span {
    padding-right: 25px;
}

.landing-button:hover span:after {
    opacity: 1;
    right: 0;
  }

.landing-button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.4s;
}
.landing-button span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }


/* Projects Page */
.text-bg {
    background-color: #141E30;
  }

.projects-grid {
    display: flex;
    flex-flow: row wrap;
}




@media only screen and (max-width: 768px) {
    .banner-text h1 {
      font-size: 44px;
    }
  
    .social-links i {
      color: white;
      font-size: 3em;
      padding: 10px;
    }
  } 

/* Resume*/
.resume-right-col {
    background: #27221F;
    color: white;
    padding: 2em;
}

.resume-avatar {
    width:375px;
    height:300px;
    object-fit:cover;
