}

/*Contact*/

.contact-grid {
    text-align: center;
    height: 650px;
    color: black;
    margin-bottom: 20px;
}
.contact-body {
    margin: flex;
    display: fixed;
    justify-content: center;
    padding-top: 2em;
}

.contact-grid {
    text-align: center;
    height: 650px;
    color: black;
    margin-bottom: 20px;
  }

.contact-grid h2 {
    font-family: 'Anton', sans-serif;
    text-align: center;

}

.contact-grid p {
    font-family: 'Oxygen', sans-serif;
    font-weight: bold;
}

.contact-grid hr {
    border-top: 3px  black;
    width: 50%;
    margin: auto;
}

.contact-list {
    display: flex;
    justify-content: center;
    color: black;
    margin-bottom: 20px;
    
}

.contact-list i {
    font-size: 35px;
    padding-right: 3rem;

}
.contact-list a {
    text-decoration: none;
    color: blueviolet;
}

.contact-avatar {
    width:300px;
    height:300px;
    object-fit:cover;
    border-radius: 60%;
    padding-top: 2em;
}

/* Landing */
.avatar-img {
    width:250px;
    height:250px;
    object-fit:cover;
    border-radius:63% 37% 68% 32% / 67% 39% 61% 33%;
    padding-top: 5em;
  }

.banner-text {
    font-size: 66px;
    font-weight: bold;
    color: white;
}

.banner-text hr {
    border-top:  5px white;
    width: 50%;
    margin: auto;
}

.banner-text p {
    color: white;
    font-size: 20px;
    padding: 1em;
}

.social-links {
    display: center;
    justify-content: space-between;
    width: 50%;
    margin: auto;
}

.social-links i {
    color: white;
    font-size: 1em;
    padding: 8px;
}